<template>
	<div class="response-container">
		<div class="response-header">
			<div class="response-zhong">
				<div class="response-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="response-ti">问答详情</div>
				<div></div>
			</div>
		</div>
		<div class="response-content">
			<div>
				<div class="response-shang">{{discussion_data.discussion_title}}</div>
				<div class="response-bj">
					<div class="response-wenz">{{discussion_data.discussion_content}}</div>
					<!--<div class="response-around">
						<div class="response-tu" @click="openSimpleDialog(data)">
							<img :src="!!discussion_data.discussion_pic?baseUrl+discussion_data.discussion_pic:'../assets/img_tl1.png'">
							<img src="../assets/img_tl1.png">
						</div>
					</div>-->
					<div class="response-wap">
						<div class="response-around" v-for="item in discussion_data.img_list">
							<div class="response-tupian" @click="openSimpleDialog(item)">
								<!--<img src="../assets/img_tl3.png">-->
								<img :src="baseUrl+item">
							</div>
						</div>
					</div>
					<mu-container>
					  <mu-dialog width="100%" :open.sync="openSimple">
					  	<div class="response-lun">
					  		<mu-carousel interval='80000'>
								<mu-carousel-item v-for="(item,index) in discussion_data.img_list" :key="index">
									<img :src="baseUrl+item" @click="openBotttomSheet(item)">
								</mu-carousel-item>
							</mu-carousel>
					  	</div>
					  	<!--<div class="response-tantu">
					  		<img :src="!!img_url?baseUrl+img_url:'../assets/img_tl1.png'">
					  	</div>-->
					  </mu-dialog>
					</mu-container>
					<mu-bottom-sheet :open.sync="open">
				   		 <mu-list @item-click="closeBottomSheet">
				      		<mu-sub-header>
				      			<div class="response-between">
				      				<div class="response-flex" @click="save()">
					      				<div class="response-xiazai">
					      					<img src="../assets/xiazai.png">
					      				</div>
					      				<div class="response-baocun">保存</div>
					      			</div>
					      			<div class="response-qux" @click=" closeSimpleDialog()">取消</div>
				      			</div>
				      		</mu-sub-header>
				    	</mu-list>
					</mu-bottom-sheet>
					<div class="response-between">
						<div class="response-flex">
							<div class="response-niming" v-if="discussion_data.discussion_anonymity==0">匿名</div>
							<div class="response-niming" v-else-if="discussion_data.discussion_anonymity==1">{{discussion_data.discussion_user_id}}</div>
							<!--<div class="response-niming">{{discussion_data.discussion_user_id}}</div>-->
							<div class="response-shijian">{{$root.formatDate(discussion_data.create_time)}}</div>
						</div>
						<div class="response-flex">
							<div class="response-xiaoxi">
								<img src="../assets/icon_pinglun2.png">
							</div>
							<div class="response-shuliang">{{discussion_data.discussion_count}}</div>
						</div>
					</div>
				</div>
				<div class="response-bj">
					<div class="response-di">
						<div class="response-jingcai">精彩回答</div>
					</div>
					<div v-for="item in comment_list">
						<!--<div class="response-between">
							<div class="response-dou">{{item.user.user_nickname}}</div>
							<div class="response-tan">
								<img src="../assets/sangedian.png" @click="go_report(item.comment_id,2,item.comment_content)">
							</div>
						</div>
						<div class="response-between">
							<div class="response-ni">{{item.comment_content}}</div>
							<div class="response-shi">{{$root.formatDate(item.create_time)}}</div>
						</div>-->
						<div v-if="item.comment_anonymity == '0'">
							<div class="response-flexx">
								<div class="pingluntou">
									<img :src="baseUrl+item.user.user_headpic" />
								</div>
								<div>
									<div class="response-dou">{{item.user.user_nickname}}</div>
									<div class="response-ni">{{item.comment_content}}</div>
								</div>
							</div>
						</div>
						<div v-if="item.comment_anonymity == '1'">
							<div class="response-flexx">
								<div class="pingluntou">
									<img src="../assets/toxiang-niming.png" />
								</div>
								<div>
									<div class="response-dou">匿名</div>
									<div class="response-ni">{{item.comment_content}}</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="response-footer">
			<div class="response-frame">
				<div class="response-picture">
					<img src="../assets/bi (2).png">
				</div>
				<div class="response-sousuo">
					<input type="text" placeholder="说点什么吧" v-model="content">
				</div>
			</div>
			<div class="response-fasong" @click="crt_comment()">发送</div>
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				user_info: {},
				data: {},
				discussionid: '',
				discussion_data: {},
				comment_list: [],
				content: '',
				anonymity:'0',
				openSimple: false,
				open: false,
				img_url: '',
				atlass_list: '',
				img_list: [],
				discussion_pic: []
			}
		},
		methods: {
			openSimpleDialog (item) {
				this.img_url = item;
		      	this.openSimple = true;
		    },
		    closeSimpleDialog () {
		      this.openSimple = false;
		      this.closeBottomSheet()
		    },
		    closeBottomSheet () {
		      this.open = false;
		    },
		    openBotttomSheet (url) {
		    	this.img_url = url;
		      	this.open = true;
		    },
			back() {
				window.history.back();
			},
//			go_report(){
//				this.$router.push('/report')
//			},
			go_report(comment_id,shu,title){
				this.$router.push({
					path:'/report',
					query:{
						type:comment_id,
						typeone:shu,
						typetwo: title
					}
				})
			},
//			查询提问讨论内容信息
			discussion(_id) {
				var that = this;
				var params = {
					// userid: that.user_info.user_id,
					discussionid: that.discussion_id
				};
				that.$http.post('/discussion/info_discussion', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.discussion_data = res.data.discussion;
						that.comment_list = res.data.comments;
						that.comment_list = that.comment_list.reverse();
						// that.discussion_data.forEach(function(item){
							that.discussion_data.img_list = []; 
							if (!!that.discussion_data.discussion_pic) { 
								if (that.discussion_data.discussion_pic.indexOf(',') >=0) { 
									
									that.discussion_data.img_list = that.discussion_data.discussion_pic.split(',');
								}else{
									
									that.discussion_data.img_list.push(that.discussion_data.discussion_pic);
								}
							}

						// })
					}
				})
			},
//			进行评论
			crt_comment() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
					discussionid: that.discussion_id,
					content: that.content,
					anonymity:that.anonymity,
				};
				that.$http.post('/discussion/crt_comment', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						this.discussion();
						this.$toast.success('发送成功');
						that.content = ''
						//						that.special_list = res.data;
					}else {
						this.$alert('发送失败', '提示');
					}
				})
			},
			//			保存图片
			save(img_url){
				var that = this
				dsBridge.call('saveImg',that.baseUrl+that.img_url);
			}
		},
		created() {
			// 页面刚加载的时候执行
			this.discussion_id = this.$route.params.id;
			this.discussion();
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.response-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.response-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.response-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.response-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.response-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	.response-tan img{
		width: 0.25rem;
		height: 0.8rem;
		margin-right: 1.5rem;
		margin-top: 1.8rem;
	}
	
	.response-content {
		width: 100%;
		height: calc(100% - 105px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.response-shang {
		font-size: 1.8rem;
		font-weight: 600;
		color: #000000;
		background: #FFFFFF;
		padding: 1rem 0 0.5rem 1.5rem;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.response-bj {
		background: #FFFFFF;
		margin-bottom: 1rem;
	}
	
	.response-wenz {
		font-size: 1.4rem;
		color: #8e8c8c;
		padding: 0.3rem 2.6rem 1.2rem 1.5rem;
	}
	
	.response-around {
		display: flex;
		justify-content: space-around;
	}
	
	.response-tu img {
		width: auto;
		height: 9rem;
		margin-bottom: 1rem;
	}
	
	.response-between {
		display: flex;
		justify-content: space-between;
	}
	
	.response-flex {
		display: flex;
	}
	
	.response-niming {
		font-size: 1.2rem;
		color: #8A8A8A;
		padding-left: 1.5rem;
		padding-right: 1rem;
	}
	
	.response-shijian {
		font-size: 1.2rem;
		color: #8A8A8A;
	}
	
	.response-xiaoxi img {
		width: auto;
		height: 1.2rem;
	}
	
	.response-shuliang {
		font-size: 1.2rem;
		color: #fc5d98;
		padding: 0 1rem 1.1rem 0.8rem;
	}
	
	/*.response-di {
		padding-top: 2rem;
		padding-bottom: 1rem;
		margin-bottom: 0.5rem;
		border-bottom: 5px solid #EAEAEA;
	}*/
	
	.response-jingcai {
		font-size: 1.4rem;
		color: #000000;
		/*border-left: 5px solid #8cadf5;*/
		border-bottom: 1px solid #EAEAEA;
		padding: 0.5rem 0;
		padding-left: 1.3rem;
	}
	
	.response-dou {
		font-size: 1.3rem;
		color: #fc5d98;
		padding: 1.4rem 0 1rem 1rem;
	}
	
	.response-ni {
		font-size: 1.2rem;
		color: #000;
		padding-left: 1rem;
		padding-bottom: 1rem;
		line-height: 0.5rem;
	}
	
	.response-shi {
		font-size: 1.2rem;
		color: #8A8A8A;
		padding-right: 1.5rem;
		padding-bottom: 1rem;
	}
	.response-footer{
		height: 55px;
		box-sizing: border-box;
		overflow: hidden;
		position: fixed;
		bottom: 0;
		width: 100%;
		background: #F2F2F2;
		border-top: 1px solid #f0f0f0;
		display: flex;
		justify-content: space-around;
		align-items: center;
	}
	.response-fasong {
		font-size: 1.5rem;
		color: #fc5d98;
		border: 1px solid #FFFFFF;
		background: #FFFFFF;
		border-radius: 10px;
		padding: 0.3rem 1rem;
		font-weight: 550;
	}
	.response-frame{
		display: flex;
		border: 1px solid #F2F2F2;
		background: #FFFFFF;
		border-radius: 10px;
		width: 65%;
	}
	.response-picture img{
		width: auto;
		height: 1.5rem;
		margin: 0.5rem;
		padding-top: 0.2rem;
	}
	.response-sousuo input{
		border: none;
		outline: none;
		background: #FFFFFF;
		margin-top: 0.5rem;
		width: 100%;
	}
	.response-wap{
		display: flex;
		flex-wrap: wrap;
	}
	.response-around  {
		display: flex;
		justify-content: space-around;
		width: 33.33%;
		/*padding-top: 1rem;*/
		padding-bottom: 1rem;
	}
	.response-tupian img{
		width: 100%;
		height: 10rem;
		padding: 0 0.5rem;
		border-radius: 15px;
	}
	.response-tantu{
		margin-bottom: -1rem;
	}
	.response-tantu img{
		width: 100%;
		height: auto;
		margin: 0 auto;
	}

	.response-xiazai img{
		height: 2rem;
		width: auto;
		margin-top: 1rem;
		margin-right: 1rem;
	}
	.response-baocun{
		font-size: 1.5rem;
		color: #000;
		/*text-align: center;*/
	}
	.response-qux{
		font-size: 1.5rem;
		padding-right: 1.5rem;
		color: #000000;
	}
	/deep/ .mu-dialog-body{
		padding: 0;
	}
	/deep/ .mu-dialog{
		max-width : 100%;
		background: #000000;
		-webkit-box-shadow: none;
		box-shadow: none;
	}
	/*轮播图*/
	.response-lun{
		width: 100%;
		height: 100%;
		position: relative;
	}
	.response-lun img {
		width: 100%;
		/*height: 100%;*/
		height: auto;
		margin: 0 auto;
	}
	/*.response-lun{
		width: 100%;
		height: 400px;
		position: relative;
	}*/
	
	/deep/ .mu-carousel {
		width: 100%;
		height: 75rem;
	}
	/deep/ .mu-tab {
		min-width: 0;
	}
	/*/deep/ .mu-carousel-indicators{
		position: absolute;
		right: 0;
		bottom: 0;
		left: 5rem;
	}*/
	
	/deep/ .mu-carousel-indicator-icon {
		background: #fe4563;
	}
	
	/deep/ .mu-carousel-button.mu-icon-button {
		font-size: 0;
		z-index: 0;
	}
	.pingluntou{
		/*width: 2rem;*/
		/*height: 2rem;*/
		display: flex;
		align-items: center;
		padding-left: 1rem;
	}
	.pingluntou img{
		border-radius: 50%;
		height: 3rem;
		width: 3rem;
		margin-top: 0.5rem;
	}
	.response-flexx{
		display: flex;
		border-bottom: 1px solid #EAEAEA;
	}
</style>


